import React, { Component } from "react";
import {
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarNav,
  MDBNavbarToggler,
  MDBCollapse,
  MDBNavItem,
  MDBNavLink,
} from "mdbreact";
import logo from "../images/lightlogo.png";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { faArrowRight } from "@fortawesome/free-solid-svg-icons";

class Nav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      collapse: false,
      isWideEnough: false,
    };
    this.onClick = this.onClick.bind(this);
  }

  onClick() {
    this.setState({
      collapse: !this.state.collapse,
    });
  }
  render() {
    return (
      <>
        <header>
          <MDBNavbar
            color="bg-primary"
            fixed="top"
            dark
            expand="md"
            scrolling
            transparent
          >
            <div
              className="wrapss"
              style={{
                paddingBottom: "-20px !important",
                marginBottom: "-20px !important",
              }}
            >
              {" "}
              <MDBNavbarBrand href="/">
                <strong>
                  <Link className="links" to="/">
                    <img src={logo} alt="LightWeb Logo" />
                  </Link>
                </strong>
              </MDBNavbarBrand>
              {!this.state.isWideEnough && (
                <MDBNavbarToggler onClick={this.onClick} />
              )}
              <MDBCollapse
                className="collaper"
                isOpen={this.state.collapse}
                navbar
              >
                <MDBNavbarNav className="hanger" right>
                  <MDBNavItem>
                    <MDBNavLink to="/services">
                      {" "}
                      <Link className="link" to="/services">
                        Services
                      </Link>
                    </MDBNavLink>
                  </MDBNavItem>
                  <MDBNavItem>
                    <MDBNavLink to="#">
                      {" "}
                      <Link className="link" to="/case-study">
                        Case Studies
                      </Link>
                    </MDBNavLink>
                  </MDBNavItem>
                  <MDBNavItem>
                    <MDBNavLink to="#">
                      {" "}
                      <Link className="link" to="/contact">
                        Contact Us
                      </Link>
                    </MDBNavLink>
                  </MDBNavItem>
                  <nav className="bigger">
                    <ul>
                      <li>
                        Request a quotation
                        <FontAwesomeIcon
                          style={{
                            marginLeft: "4px",
                          }}
                          icon={faArrowRight}
                        />
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                      </li>
                    </ul>
                  </nav>
                </MDBNavbarNav>
              </MDBCollapse>
            </div>
          </MDBNavbar>
        </header>
      </>
    );
  }
}

export default Nav;
